<template>
    <div class="show-tasks">
        <h1>Show Tasks</h1>
        <table v-if="tasks.length > 0">
            <thead>
                <tr>
                    <td>Text</td>
                    <td>From</td>
                    <td>To</td>
                    <td>Created At</td>
                    <td>Actions</td>
                </tr>
            </thead>
            <tbody>
                <tr
                    v-for="(task, index) in tasks"
                    :key="task.id"
                    :class="{ yes: task.completed }"
                >
                    <td>{{ task.taskText }}</td>
                    <td>{{ task.from }}</td>
                    <td>{{ task.to }}</td>
                    <td>{{ new Date(task.createdAt).toLocaleDateString() }}</td>
                    <td>
                        <button
                            class="completed"
                            @click="markAsCompleted(task)"
                        >
                            {{ task.completed ? "Incompleted" : "Completed" }}
                        </button>
                        <button class="del" @click="deleteTask(index)">
                            Delete
                        </button>
                    </td>
                </tr>
            </tbody>
        </table>
        <h2 v-else style="margin-top: 70px">No Tasks To Show</h2>
    </div>
</template>
<script setup>
import todoFunction from "@/mixins/todoMixin";
const { tasks, addToLocalSt } = todoFunction();

const deleteTask = (index) => {
    tasks.value.splice(index, 1);
    addToLocalSt();
};

const markAsCompleted = (task) => {
    task.completed = !task.completed;
    addToLocalSt();
};
</script>
<style scoped lang="scss">
table {
    width: 100%;
    tr {
        &.yes {
            background-color: #eee;
        }
        td {
            padding: 5px;
            border: 1px solid;
            button {
                padding: 5px 10px;
                background-color: #15bdd3bd;
                border: 1px solid #15bdd3bd;
                color: white;
                font-size: 16px;
                border-radius: 6px;
                cursor: pointer;
                &.del {
                    background-color: indianred;
                    border: 1px solid indianred;
                    margin-left: 10px;
                }
            }
        }
    }
}
</style>
